@import url("https://fonts.googleapis.com/css2?family=Almendra+SC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=M+PLUS+1p&display=swap");

html {
	height: 100%;
	scroll-behavior: smooth;
}
body {
	background-color: #333333;
	color: beige;
	
	overflow-x: hidden;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	
}
section {
	min-height: 100vh !important;
}
.row,
.container-fluid {
	margin-left: 0px !important;
	margin-right: 0px !important;
	padding-left: 0px !important;
	padding-right: 0px !important;
}

hr {
	height: 60px !important;
	width: 100%;
	color: antiquewhite;
	-webkit-box-shadow: 10px 10px 10px #000;
	        box-shadow: 10px 10px 10px #000;
	margin-top: 4rem;
	margin-bottom: 4rem;
}
p {
	font-size: 18px;
	padding-top: 5px;
	padding-bottom: 20px;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	letter-spacing: 2px;
	line-height: 1;
	text-align: center;
}
.card_link {
	color: #fff;
	font-size: 16px;
	padding: 2rem;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	letter-spacing: 2px;
	line-height: 2;
	font-weight: 600;
	cursor: pointer;
}
.card_link:hover {
	text-decoration: underline;
	color: #333333;
}
label {
	font-size: 20px;
	margin-top: 1rem;
}
.title {
	font-size: 30px;
	padding-top: 2rem;
	font-weight: 600;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	letter-spacing: 2px;
	text-align: center;
	margin-bottom: 1rem;
}
@media (max-width: 576px) {
	.title {
		font-size: 20px;
		padding-top: 1rem;
		font-weight: 500;
	}
	p {
		font-size: 16px;
		
}
.img_gallery {
	width: 100%;
}
}
@media (min-width: 576px) {
	.img_gallery {
		width: 100%;
	}
}
@media (min-width: 768px) {
	.img_gallery {
		width: 80%;
	}
}
@media (min-width: 900px) {
	.img_gallery {
		width: 70%;
	}
}



