.info {
	font-size: 16px;
	padding-top: 2rem;
	text-decoration: none;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	letter-spacing: 2px;
	line-height: 2;
}

#pic-home {
	display: -ms-flexbox;
	display: flex;

	height: auto;
	width: 60vw;
}

@media (max-width: 576px) {
	#pic-home {
		display: -ms-flexbox;
		display: flex;
		height: auto;
		width: 100%;
	}
	.info {
		font-size: 14px;
}
}


