.heading {
	font-size: 2.5rem;
	font-family: "M PLUS 1p", sans-serif;
	letter-spacing: 2px;
	font-weight: 700;
	line-height: 1;
	background-color: #00000060;
	position: absolute;
	padding: 20px;
	top: 57%;
	border-radius: 15px;
	right: 5%;
	z-index: 5;
	-webkit-animation-name: titleAppears;
	        animation-name: titleAppears;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
}
.sub-heading {
	font-size: 2.2rem;
	padding: 20px;
	text-decoration: underline;
	font-family: "M PLUS 1p", sans-serif;
	letter-spacing: 2px;
	line-height: 1;
	background-color: #00000060;
	position: absolute;
	border-radius: 15px;
	top: 70%;
	right: 5%;
	z-index: 5;
	-webkit-animation-name: subtitleAppears;
	        animation-name: subtitleAppears;
	-webkit-animation-duration: 4s;
	        animation-duration: 4s;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
}

@-webkit-keyframes titleAppears {
	from {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(0rem);
		        transform: translateX(0rem);
	}
}

@keyframes titleAppears {
	from {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(0rem);
		        transform: translateX(0rem);
	}
}
@-webkit-keyframes subtitleAppears {
	0% {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	50% {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateX(0rem);
		        transform: translateX(0rem);
	}
}
@keyframes subtitleAppears {
	0% {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	50% {
		opacity: 0;
		-webkit-transform: translateX(5rem);
		        transform: translateX(5rem);
	}
	100% {
		opacity: 1;
		-webkit-transform: translateX(0rem);
		        transform: translateX(0rem);
	}
}

@media (max-width: 576px) {
	.heading {
		font-size: 2rem;
		letter-spacing: 2px;
		padding: 10px;
		top: 50%;
		right: 5%;
	}
	.sub-heading {
		font-size: 1.3rem;
		padding: 10px;
		letter-spacing: 2px;
		top: 75%;
		right: 5%;
	}
}
@media (min-width: 576px) {
	.heading {
		font-size: 2rem;
		letter-spacing: 2px;
		padding: 10px;
	}
	.sub-heading {
		font-size: 1.6rem;
		padding: 10px;
	}
}

@media (min-width: 768px) {
	.heading {
		font-size: 2rem;
		letter-spacing: 2px;
		padding: 10px;
		top: 50%;
		right: 5%;
	}
	.sub-heading {
		font-size: 1.6rem;
		padding: 10px;
		top: 60%;
		right: 5%;
	}
}
@media (min-width: 998px) {
	.heading {
		font-size: 2.5rem;
		letter-spacing: 2px;
		padding: 10px;
		top: 40%;
		right: 5%;
	}
	.sub-heading {
		font-size: 2rem;
		padding: 10px;
		top: 50%;
		right: 5%;
	}
}
@media (min-width: 1400px) {
	.heading {
		top: 50%;
		right: 5%;
	}
	.sub-heading {
	top: 60%;
		right: 5%;
	}
}


