.navbar {
	background-color: #252525;
	color: #1f1f1f !important;
	border-top: 2px rgb(130, 252, 215);
	border-bottom: 2px rgb(130, 252, 215);
	border-style: solid;
}

.navbar-brand {
	font-family: "Almendra SC", serif;
	padding-left: 30px !important;
	font-size: 3rem;
	font-weight: 700;
	color: rgb(130, 252, 215) !important;
	-webkit-text-stroke: 0.7px rgb(0, 5, 46);
}
#added-nav-item {
	display: none;
}
.btn {
	background-color:  #252525;
}
.fas {
	color: rgb(139, 139, 139);
	padding-right: 3rem;
	font-size: 30px !important;
}
.fas:hover {
	color: #eeeeee;
}
.fas:active {
	color: #eeeeee;
}
@media (max-width: 576px) {
	#navbar_icons {
		display: none;
	}
	#added-nav-item {
		display: block;
	}
	.navbar-brand {
		font-size: 2rem;
	}
}
@media (min-width: 576px) {
	#navbar_icons {
		display: none;
	}
	#added-nav-item {
		display: block;
	}
}
@media (min-width: 768px) {
	#navbar_icons {
		display: none;
	
	}
	#added-nav-item {
		display: block;
	}
}
@media (min-width: 1200px) {
	#navbar_icons {
		display: -ms-inline-flexbox;
		display: inline-flex;
	}
	#added-nav-item {
		display: none;
	}
}



