.nav-link {
	font-size: 17px;
	color: #949494 !important;
	margin-right: 3rem;
	margin-left: 2rem;
	font-weight: 600;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	letter-spacing: 2px;
}
.nav-link:hover {
	text-decoration: underline;
	color: #eeeeee !important;
}
@media (max-width: 576px) {
	.nav-link {
		font-size: 14px;
	}
}


