.card {
	background-color: #00afa0;
	padding: 1rem 2rem;
	border-radius: 20px;
	margin-top: 3rem;
}
@media (max-width: 576px) {
	.card {
		padding: 0rem 2rem;
		border-radius: 0px;
		margin-left: 0rem;
		margin-right: 0rem;
		margin-top: 0rem;
	}
}
label {
	display: -ms-flexbox;
	display: flex;
	justify-self: left !important;
}



